#drum-machine {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #282c34;
  color: white;
}

#display {
  margin-bottom: 20px;
  padding: 10px;
  width: 300px;
  text-align: center;
  border: 2px solid white;
}

.pad-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.drum-pad {
  background-color: #444;
  border: 2px solid #888;
  padding: 20px;
  text-align: center;
  font-size: 1.5em;
  cursor: pointer;
  user-select: none;
}

.drum-pad:active {
  background-color: #666;
}
